<template>
  <div>
    <div class="art-lis">
      <!--  -->
      <h2 class="art-title">{{detailData.article_title}}</h2>
      <!--  -->
      <p class="art-content">
        {{detailData.article_content}}
      </p>
      <!--  -->
      <div class="art-icon">
        <span class="heat-icon">
          <i class="iconfont icon-icon" title=""></i>
        </span>
        <span class="article-heat">{{art.article_heat}}</span>
        <span class="creat-time">
          <i class="iconfont icon-dingdanxiangqing-chuangjianshijian" title=""></i>
          {{detailData.create_time}}
        </span>
        <span class="update-time">
          <i class="iconfont icon-gengxinshijian" title=""></i>
          {{detailData.update_time}}
        </span>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  computed: {
    detailData(){
      return this.$route.query;
    }
  }
};
//homepage
</script>
<style scoped>
.art-lis {
  margin-left: 120px;
  margin-top: 30px;
  width: 730px;
  background-color: #f9f9f9;
  padding: 25px 25px;
}
.art-content {
  line-height: 30px;
  color: #404040;
  font-size: 14px;
  text-indent: 20px;
  margin-top: 20px;
  margin-bottom: 20px;
  letter-spacing: 2px;
}

.art-title {
  font-family: -apple-system, BlinkMacSystemFont, "Apple Color Emoji",
    "Segoe UI Emoji", "Segoe UI Symbol", "Segoe UI", "PingFang SC",
    "Hiragino Sans GB", "Microsoft YaHei", "Helvetica Neue", Helvetica, Arial,
    sans-serif;
  margin-bottom: 5px;
}
.art-icon {
  color: #b4b4b4;
  font-size: 12px;
}
.art-icon i {
  margin-left: 8px;
  font-size: 11px;
}
.heat-icon i {
  color: #ea6f5a;
}
</style>


