<template>
  <div class="login">
    <!--  -->
    
    <!--  -->
    <diV>
      <!-- <router-link to="/login/loginname"></router-link>
      <router-link to="/login/loginmail"></router-link>
      <router-view></router-view>-->
      <!--  -->
      <a href="javascript:;" class="login-type" @click="show = true" :class="{on:show}"></a>
      <a href="javascript:;" class="login-type" @click="show = false" :class="{on: !show}"></a>
    </diV>

    <div class="noshow" :class="{on:show}">
      <!-- input -->
      <el-row>
        <el-col :span="8">
          <label for="userName"></label>
        </el-col>
        <el-col :span="16">
          <el-input placeholder="" v-model="userName" id="userName" clearable></el-input>
        </el-col>
      </el-row>

      <el-row>
        <el-col :span="8">
          <label for="userPassword"></label>
        </el-col>
        <el-col :span="16">
          <el-input placeholder="" v-model="userPassword" id="userPassword" show-password></el-input>
        </el-col>
      </el-row>
    </div>

    <div class="noshow" :class="{on: !show}">
      <!-- input -->
      <el-row>
        <el-col :span="8">
          <label for="userEmail"></label>
        </el-col>
        <el-col :span="16">
          <el-input placeholder="" v-model="userEmail" id="userEmail" clearable></el-input>
        </el-col>
      </el-row>

      <el-row>
        <el-col :span="8">
          <label for="userPassword"></label>
        </el-col>
        <el-col :span="16">
          <el-input placeholder="" v-model="userPassword" id="userPassword" show-password></el-input>
        </el-col>
      </el-row>
    </div>
    <el-button  @click="login" round></el-button>
    <!--  -->
    <router-link to = "/forgetpsd"></router-link>
    <!-- <a href="javascript:;" @click="forgetpsd"></a> -->
    <!--  -->
    <!-- <router-link @click='changepsd'></router-link> -->
  </div>
</template>

<script>
import qs from "qs";
import { ajax } from "../network/request";

export default {
  name: "login",
  data() {
    return {
      input: "",
      show: true, //truefalse
      userName: "",
      userEmail: "",

      userPassword: "",
    };
  },
  methods: {
    login() {
      if (this.show) {
        //
        let data = {
          userName: this.userName,
          userPassword: this.userPassword
        };
        ajax({
          url: "/user/login",
          method: "post",
          data: qs.stringify(data),
          // headers:{'token':"aaaaaaaaaa"}
        })
          .then(res => {
            console.log(res);
            // let res = JSON.parse(response);
            if (res.status == 0) {
              //tokenvuex   ?
              this.$store.commit('hasLogin',res.data)
              alert(",token" + res.data);
            }
          })
          .catch(err => {
            alert("");
            console.log(err)
          });
      } else {
        //
        
      }
    },
    forgetpsd(){
      this.$router.replace = "/forgetpsd"
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
label {
  line-height: 40px;
  text-align-last: justify;
  text-align: justify;
}
.login {
  font-size: 14px;
}
.login-type{
  float: left;
  /* margin: 5px 5px; */
  margin-left: 5px;
}
.noshow {
  display: none;
}
.on {
  display: block;
}
.el-row {
  margin-top: 20px;
}
</style>



